//  Grow
@keyframes hideGrow {
  from {
    transform: scale(1);
    opacity: 1;
  }
  to {
    transform: scale(0.5);
    opacity: 0;
  }
}

@keyframes showGrow {
  from {
    transform: scale(0.5);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

//  Fade

@keyframes hideFade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes showFade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
