@import "sass/index";
@import "./animations";

$animationTime: 0.15s;
$playAnimation: $animationTime ease-in-out forwards;

.grow {
  &.playHide {
    animation-name: hideGrow;
  }
  &.playShow {
    animation-name: showGrow;
  }
}

.fade {
  &.playHide {
    animation-name: hideFade;
  }
  &.playShow {
    animation-name: showFade;
  }
}

.playHide {
  animation: $playAnimation;
  pointer-events: none;
}

.playShow {
  animation: $playAnimation;
}

.disable {
  opacity: 0;
  pointer-events: none;
}
